@import url(https://fonts.googleapis.com/css?family=Crimson+Text|Work+Sans&display=swap);
.grow {
	width: 200px;
	display: block;
}

.project-container {
	margin: 0 10px;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
}

.project-title {
    font-size: large;
    margin: 10px 0 0;
}

.image-size {
	max-width: 200px;
	max-height: 120px;
	padding: 5px 0px;
	overflow: hidden;
}

.project-links {
	display: flex;
	justify-content: space-evenly;
	margin: 10px 0 10px;
}

.card-link {
    -webkit-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}



* {
	font-family: 'Work Sans', sans-serif;
}

h3,
h4 {
	font-family: 'Crimson Text', serif;
}


.imgcenter {
	margin: 0 25%;
}


@media only screen and (max-width: 600px) {
	.navtext {
		font-size: 80% !important; 
	}
	
	.sfull {
		margin: 5% 0;
	}
	
	.acenter {
		margin-top: 10%;
	}
	
	.cc-space {
		margin-top: 5%;
	}
	
	.pcenter {
		padding: 0 10%;
	}
	
	.vcenter {
		margin-top: 5%;
		padding: 15% 0;
	}

	.social {
		padding: 0 10% 0 0;
	}
} 

@media only screen and (min-width: 600px) {
	.acenter {
		margin-top: 5%;
	}
	
	.pcenter {
		padding: 0 10%;
	}
	
	.vcenter {
		margin-top: 5%;
		padding: 15% 0;
	}
	
	.social {
		padding: 0 10% 0 0;
		
	}
} 

@media only screen and (min-width: 1200px) {
	.vcenter {
		margin-top: 5%;
		padding: 15% 0;
	}
}
